import React, { useState } from 'react'
import {
  Stack,
  Group,
  Title,
  Text,
  Button,
  Card,
  Badge,
  ActionIcon,
  TextInput,
  Grid,
  Center,
  Box,
  Paper,
  Divider,
  Modal,
  Tabs
} from '@mantine/core';
import {
  IconTemplate,
  IconSearch,
  IconCopy,
  IconEye,
  IconEdit,
  IconPlus,
  IconRobot
} from '@tabler/icons-react';
import { notifications } from '@mantine/notifications';
// 暂时注释掉PageLayout导入，直接使用Stack布局
// import { PageLayout } from '../../../apps/desktop/src/components/layout/PageLayout';

const mockTemplates = [
  {
    id: 1,
    title: '文章写作助手',
    description: '帮助您创作高质量的文章内容',
    category: '写作',
    template: '请帮我写一篇关于 {主题} 的文章，要求：\n1. 结构清晰，逻辑严谨\n2. 内容深入，观点独特\n3. 语言流畅，易于理解\n4. 字数控制在 {字数} 字左右'
  },
  {
    id: 2,
    title: '代码审查专家',
    description: '专业的代码审查和优化建议',
    category: '编程',
    template: '请审查以下代码并提供改进建议：\n\n```{编程语言}\n{代码内容}\n```\n\n请从以下角度分析：\n1. 代码质量和可读性\n2. 性能优化建议\n3. 安全性考虑\n4. 最佳实践建议'
  },
  {
    id: 3,
    title: '数据分析师',
    description: '专业的数据分析和洞察提取',
    category: '分析',
    template: '请分析以下数据并提供洞察：\n\n{数据描述}\n\n分析要求：\n1. 识别关键趋势和模式\n2. 提供数据驱动的洞察\n3. 给出可行的建议\n4. 用图表或可视化方式展示结果'
  },
  {
    id: 4,
    title: '翻译专家',
    description: '专业的多语言翻译服务',
    category: '翻译',
    template: '请将以下内容从 {源语言} 翻译成 {目标语言}，要求：\n1. 准确传达原文意思\n2. 符合目标语言表达习惯\n3. 保持原文的语气和风格\n\n原文：\n{待翻译内容}'
  },
  {
    id: 5,
    title: '学习助手',
    description: '帮助理解和学习复杂概念',
    category: '教育',
    template: '请帮我理解 {概念/主题}，要求：\n1. 用简单易懂的语言解释\n2. 提供具体的例子\n3. 指出重点和难点\n4. 给出学习建议\n\n我的背景：{学习背景}'
  }
]

export const PromptTemplateManager: React.FC = () => {
  const [selectedCategory, setSelectedCategory] = useState<string>('全部')
  const [searchTerm, setSearchTerm] = useState<string>('')
  const [selectedTemplate, setSelectedTemplate] = useState<any>(null)
  const [viewModalOpen, setViewModalOpen] = useState(false)
  // 原弹窗创建入口已移除，改为二级页面

  const categories = ['全部', '写作', '编程', '分析', '翻译', '教育']

  const filteredTemplates = mockTemplates.filter(template => {
    const matchesCategory = selectedCategory === '全部' || template.category === selectedCategory
    const matchesSearch = template.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         template.description.toLowerCase().includes(searchTerm.toLowerCase())
    return matchesCategory && matchesSearch
  })

  const handleCopyTemplate = (template: string) => {
    navigator.clipboard.writeText(template)
    notifications.show({
      title: '复制成功',
      message: '模板已复制到剪贴板',
      color: 'green'
    })
  }

  return (
    <Stack gap="lg" p="md">
      {/* 头部操作栏 */}
      <Group justify="space-between">
        <Group>
          <IconTemplate size={28} color="var(--mantine-color-green-6)" />
          <Box>
            <Title order={2}>提示词模板库</Title>
            <Text c="dimmed" size="sm">精选的 AI 提示词模板，提升您的工作效率</Text>
          </Box>
        </Group>
        
        <Group>
          <Button
            leftSection={<IconRobot size={16} />}
            variant="gradient"
            gradient={{ from: 'green', to: 'teal' }}
            onClick={() => {
              if (typeof window !== 'undefined') {
                window.location.href = '/templates/new?mode=ai'
              }
            }}
          >
            AI 生成
          </Button>
          <Button
            leftSection={<IconPlus size={16} />}
            onClick={() => {
              if (typeof window !== 'undefined') {
                window.location.href = '/templates/new'
              }
            }}
          >
            创建模板
          </Button>
        </Group>
      </Group>

      {/* 搜索和筛选 */}
      <Group>
        <TextInput
          placeholder="搜索模板..."
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.currentTarget.value)}
          leftSection={<IconSearch size={16} />}
          style={{ flex: 1 }}
        />
      </Group>

      {/* 分类标签 */}
      <Tabs value={selectedCategory} onChange={(value) => setSelectedCategory(value || '全部')}>
        <Tabs.List>
          {categories.map(category => (
            <Tabs.Tab key={category} value={category}>
              {category}
            </Tabs.Tab>
          ))}
        </Tabs.List>
      </Tabs>

      {/* 模板列表 */}
      {filteredTemplates.length === 0 ? (
        <Card shadow="sm" padding="xl" radius="md" withBorder>
          <Center>
            <Stack align="center" gap="md">
              <IconTemplate size={64} color="var(--mantine-color-gray-5)" />
              <Title order={3} ta="center">没有找到匹配的模板</Title>
              <Text c="dimmed" ta="center" maw={400}>
                尝试调整搜索条件或选择其他分类
              </Text>
            </Stack>
          </Center>
        </Card>
      ) : (
        <Grid>
          {filteredTemplates.map(template => (
            <Grid.Col key={template.id} span={{ base: 12, md: 6, lg: 4 }}>
              <Card shadow="sm" padding="lg" radius="md" withBorder h="100%">
                <Stack gap="sm" h="100%">
                  <Group justify="space-between">
                    <Badge variant="light" color="blue" size="sm">
                      {template.category}
                    </Badge>
                  </Group>

                  <Box style={{ flex: 1 }}>
                    <Title order={4} mb="xs">{template.title}</Title>
                    <Text size="sm" c="dimmed" mb="sm" lineClamp={2}>
                      {template.description}
                    </Text>
                    
                    <Paper p="sm" bg="gray.0" radius="md" mb="sm">
                      <Text size="xs" c="dimmed" lineClamp={4} style={{ fontFamily: 'monospace' }}>
                        {template.template}
                      </Text>
                    </Paper>
                  </Box>

                  <Divider />

                  <Group justify="space-between">
                    <Group gap="xs">
                      <ActionIcon
                        variant="light"
                        size="sm"
                        onClick={() => {
                          // 导航到详情页面
                          if (typeof window !== 'undefined') {
                            window.location.href = `/templates/${template.id}`;
                          }
                        }}
                      >
                        <IconEye size={14} />
                      </ActionIcon>
                      <ActionIcon
                        variant="light"
                        size="sm"
                        onClick={() => {
                          // TODO: 编辑功能
                        }}
                      >
                        <IconEdit size={14} />
                      </ActionIcon>
                    </Group>
                    
                    <Button
                      size="xs"
                      leftSection={<IconCopy size={12} />}
                      onClick={() => handleCopyTemplate(template.template)}
                    >
                      复制
                    </Button>
                  </Group>
                </Stack>
              </Card>
            </Grid.Col>
          ))}
        </Grid>
      )}

      {/* 查看详情模态框 */}
      <Modal
        opened={viewModalOpen}
        onClose={() => setViewModalOpen(false)}
        title={selectedTemplate?.title || '模板详情'}
        size="lg"
      >
        {selectedTemplate && (
          <Stack gap="md">
            <Group>
              <Badge color="blue">{selectedTemplate.category}</Badge>
            </Group>
            
            <Text>{selectedTemplate.description}</Text>
            
            <Box>
              <Text fw={500} mb="sm">模板内容：</Text>
              <Paper p="md" withBorder bg="gray.0">
                <Text size="sm" style={{ whiteSpace: 'pre-wrap', fontFamily: 'monospace' }}>
                  {selectedTemplate.template}
                </Text>
              </Paper>
            </Box>
            
            <Group justify="flex-end">
              <Button
                leftSection={<IconCopy size={16} />}
                onClick={() => {
                  handleCopyTemplate(selectedTemplate.template)
                  setViewModalOpen(false)
                }}
              >
                复制模板
              </Button>
            </Group>
          </Stack>
        )}
      </Modal>

      {/* 创建模板改为二级页面 /templates/new */}
    </Stack>
  )
}